$primary-color: #2fa774;
$secondary-color: #213155;
$button-hover-color: #01153f;
$text-color: whitesmoke;

* {
  box-sizing: border-box;

  margin: 0;
  padding: 0;

}


// LOGIN

.loginWrapper {
  display: flex;
}

.loginContainer {
  margin: 10% auto;
  padding: 20px;

  border-radius: 10px;
  border: 3px solid $secondary-color;
}

.loginTitle {
  text-align: center;
}

 input, textarea {
   padding: 3px;
   margin-top: 3px;
   border: 2px solid $secondary-color;
   border-radius: 5px;
}

.loginFormField .rememberMe div {
  display: inline;
  margin-right: 5px;
}

button[type='submit'] {
  background: black;
  border-radius: 5px;
  outline: none;
  border: 2px solid transparent;
  padding: 2px 3px;
  color: whitesmoke;
  font-weight: bold;

  margin: 5px auto;
  transition: all .3s;
}

button[type='submit']:hover {
  background-color: whitesmoke;
  color: black;
  border: 2px solid black;
  cursor: pointer;
  box-shadow: 0 0 3px 1px black;
}

//FORMS CONTROL

.formControl.error textarea
{
  border: 2px solid black;
}

.formControl span {
  color: black;
}


textarea {
  min-height: 54px;
  min-width: 200px;
  max-width: 300px;
  max-height: 100px;
  resize: none;
  outline: none;
}

// APP

.appWrapper {
  max-width: 70%;
  margin: 0 auto;
}

.appWrapperContent {
  width: 90%;
}

.flexContainer {
  display: flex;
  height: 100%;
  min-height: 88vh;
  margin-top: 10px;
}


// HEADER

.Header {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 100px;
  background-color: $secondary-color;

  img {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
  }
}

.Logo {
  border: 2px solid #213155;
  border-radius: 50%;

  margin-left: 5px;
}

.loginBlock p {
  color: $text-color;
  text-decoration: none;

  margin: 10px 20px 0 0;
}

.loginAndLogout {
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: end;

  button {
    outline: none;
    border: 0;
    background: transparent;
  }

  button img {
    position: absolute;
    right: 5%;
    top: 70%;
    width: 40px;
    height: 40px;
    cursor: pointer;
  }
}

// NAV MENU

.Navbar {
  display: flex;
  flex-direction: column;
  width: 20%;
  max-height: 100vh;

  margin-right: 10px;
  padding-bottom: 20px;

  background-color: #2fa774;
  border: 3px solid #213155;
  border-radius: 10px;
}

.navList {
  margin: 15px 0 0 15px;

  li {
    list-style: none;
  }

  li:last-child {
    margin-top: 20%;
  }
}

.navItem {
  a {
    color: white;
    font-size: 28px;
    text-decoration: none;
  }

  a:hover {
    opacity: 0.7;
  }

  .activeLink {
    color: gold;
  }
}


// FRIENDS

.FriendsWrapper {
  padding: 15px;
  margin-top: 50px;
  color: white;
}

.friendsTitle {
  font-weight: 400;
  font-size: 32px;
}

.friendsList {
  display: flex;
  max-width: 100%;
}

// PROFILE PAGE

.content {
  display: flex;
  flex-direction: column;
  min-height: 88vh;
  background-color: $primary-color;
  border: 3px solid $secondary-color;
  border-radius: 10px;
}

// PROFILE STATUS

.inputWrapper {
  padding: 5px;
  max-width: 400px;

  input {
    outline: none;
  }
  span:hover {
    cursor: pointer;
    text-decoration: underline;
    background-color: #2e9569;
    box-shadow: 0 0 5px 3px #2e9569;
    border-radius: 20px;
  }
}


// PROFILE INFO

.profilePage {
  position: relative;
}

.userAvatar {
  width: 20%;
  height: 20%;

  border: 3px solid black;
  border-radius: 30%;
}

.userAvatar:hover {
  cursor: pointer;
  box-shadow: 0 0 5px 3px #213155;
}

.userPageHeader {
  display: flex;
  align-items: flex-start;
  margin: 20px 0 0 20px;
}

.nameAndStatus {
  display: flex;
  flex-direction: column;
  margin-left: 50px;

  strong {
    font-size: 28px;
  }

  p {
    font-size: 22px;
  }
}

.userName {}

.infoEditButton {
  position: absolute;
  top: 5px;
  right: 1%;

  padding: 3px 5px;
  background-color: #213155;
  outline: none;
  color: white;
  font-weight: bold;
  border: none;
  border-radius: 5px;
}

.infoEditButton:hover {
    background-color: #01153f;
    cursor: pointer;
}

.descriptionBlock {
  position: relative;
  padding: 15px;

  strong {
    font-size: 24px;
  }

  p {
    font-size: 20px;
  }
}

.jobLooker {
  width: 50%;

  strong {
    margin-bottom: 15px;
  }
}

.photoInput {}

.inputFile {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

.photoInput {
  label {
    display: inline-block;
    width: 20%;
    margin: 5px 0 0 20px;
    padding: 0;
    font-size: 1.25em;
    font-weight: 700;
    color: whitesmoke;
    background-color: #213155;
    border-radius: 8%;
    text-align: center;
  }
  :hover {
    background: $button-hover-color;
    cursor: pointer;
  }
}

.aboutMe {
  margin-top: 10px;

  b {
    font-size: 24px;
  }
}

.contactsWrapper {
  width: 50%;
  margin: 0 10% 0 0;
  padding-top: 5px;
}

.showContactsButton {
  position: absolute;
  margin-top: 5px;

  padding: 3px 5px;
  background-color: #213155;
  outline: none;
  color: white;
  font-weight: bold;
  border: none;
  border-radius: 5px;
}

.showContactsButton:hover {
  background-color: $button-hover-color;
  cursor: pointer;
}

.editProfileFormWrapper {}

//MY POSTS

.postsBlock {
  display: flex;
  flex-direction: column;
  padding: 15px;
}

.postTools h3 {
  margin-bottom: 20px;
  font-size: 26px;
}


.posts {
  margin-top: 15px;
}

// DIALOGS

.Dialogs {
  display: grid;
  grid-template-columns: 2fr 10fr;
}

.dialogsItems {
  padding: 10px;
}

.messages {}

.messageItems {
  padding: 10px;

}

.dialogItem {
  margin-bottom: 10px;
}

.active {}


//MY POSTS FORM


.formWrapper {
  display: flex;
}

.addPostForm button {
  height: 54px;
  margin-left: 5px;
  width: 10%;
  background-color: #213155;
  color: whitesmoke;
  outline: none;
}

// POST

.item {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  padding-bottom: 10px;

  .messageBody {
    margin-bottom: 10px;
  }
}

.likesCount button {
  width: 10%;
  padding: 0;
  outline: none;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  transition: all .3s;
}


.likesCount button:hover {
  transform: scale(1.25);
  cursor: pointer;
}

.likesCount button:active {
  transform: scale(1);
}

.likeIcon {
  margin-right: 5px;
}

hr {
  margin-top: 10px;
  margin-bottom: 15px;
}

.postAvatar {
  width: 90px;
  margin-right: 15px;
  border-radius: 50%;
}


// USERS


.usersPage {
  display: flex;
  flex-direction: column;
  background-color: $primary-color;
  border: 3px solid $secondary-color;
  border-radius: 10px;
  min-height: 88vh;

  img {
    width: 90px;
    height: 90px;
    border-radius: 50%;
  }

  ul li {
    margin-right: 5px;
  }
}

.usersList {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  margin: 2% 10% 0 10%;
  height: 100%;

}

/*paginator styles*/

.paginator {
  margin: 1% auto 0 auto;
}

.paginator ul {
  display: flex;
  justify-content: center;
  flex-wrap: nowrap;

  li {
    font-size: 20px;
    width: 37px;
    margin: 0 8px;
    text-align: center;
    border: 2px solid transparent;
    border-radius: 25%;
  }

  button {
    padding: 0 10px;
    color: white;
    background-color: #213155;
    border-radius: 10%;
    outline: none;
    cursor: pointer;

    //:active {
    //opacity: 0.7;
    //outline: none;
    //background: $button-hover-color;
    //}
  }
}

.pagesListItem {
  list-style: none;
  cursor: pointer;
  display: inline-block;
}

.selectedPage {
  font-weight: 700;
}


